@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@300&display=swap');
body{
  font-family: 'Dosis', sans-serif;
  margin: 0;
  padding: 0;
}

.app-container {
  text-align: center;
  width: 100vw;
  height: 100vh;
}

.form-container {
  margin-bottom: 30px;
}

.transaction-list-container div {
  margin-bottom: 10px;
}

button {
  margin-top: 10px;
}

.message{
  text-align: center;
  color: green;
}

span{
  color: red;
  font-weight: 900;
}

input:focus{
  border: 1px solid blue;
}

@media (max-width:608px) {
  
 .app-container{
  margin-left: 30px;
  padding-right:50px;
 }
}
