.header{
    margin-top: 10px;
    align-items: center;
    display: flex;
    justify-content: space-between;
    height: 80px;
    background-color: rgb(36, 82, 190);
    color: white;
    margin-bottom: 30px;
}

.nav-header{
    margin: 10px;
}

button{
    border-radius: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
    padding: 18px;
    margin-right: 30px;
}


@media (max-width: 608px) {
  
    .header{
        padding-right: 50px;
    }
    
  }

  @media (max-width: 430px) {
    .logout{
        margin-right: 0;
        margin-left:20px;
     }
   
  }

